@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  /* image taken from unsplash:width: https://images.unsplash.com/photo-1549321495-305eb13f8aa9?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80 */
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.3)),
    url(/static/media/background.5c1db516.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "Poppins", sans-serif;
  color: rgb(233, 229, 229);
}

header {
  text-align: center;
  font-size: 1.7rem;
  margin: 2rem 0;
  color: rgb(199, 213, 226);
  text-shadow: 4px 3px rgb(58, 58, 124);
}

button {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto 1rem auto;
  font-size: 1rem;
  color: #fff;
  background-color: rgba(4, 20, 59, 0.376);
  box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.924);
  padding: 0.5rem 3rem;
  border: none;
  outline: none;
  transition: all 0.4s ease 0s;
  text-transform: uppercase;
}

button:hover {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.4s ease 0s;
}

.center-text {
  text-align: center;
}

span {
  font-style: italic;
}

.card-container {
  font-size: 1.3rem;
  width: 40vw;
  margin: 0.5rem 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid black;
  box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.924);
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.error-container {
    text-align: center;
  font-size: 1.3rem;
  width: 40vw;
  margin: 0.5rem 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid black;
  box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.924);
}

input {
  width: 100%;
  max-width: 300px;
  padding: 0.7rem;
  margin-bottom: 1rem;
  border: none;
  outline: none;
  background-color: rgba(18, 27, 70, 0.7);
  border-bottom: 3px solid #000;
  font-size: 1.5rem;
  color: #fff;
  text-align: center;
}

input[type="text"]:focus {
  background-color: rgba(3, 9, 37, 0.5);
}

